	module.exports = {"data":{"markdownRemark":{"html":"<p>So I am applying to this company named Ownego and this is the exercise that I received. Basically, I need to rebuild this template.</p>\n<p><img src=\"http://res.cloudinary.com/hunghayho131/image/upload/v1530353833/ownego.jpg\" alt=\"Exercise Picture\"></p>\n<p>I need to be responsive and the red circle would be clickable. I will grow darker after every click until in turns pitch black with the 9th click.</p>\n<p>First thing come to my mind about responsive web page is Bootstrap since I have known it for quite sometimes. But I am ahead of myself once again. The most initial thing that need to be done is to construct the color boxes. In order to do so, I search w3 for css layout and I got a good one.</p>\n<p>Ultimately, with great help from my friend Dai, I was able to finish this exercise 5 hours after the deadline. It is an embarrassing experience for me but I know a thing or more about these things and I will get better for sure. Anway this is the solution:</p>\n<p><a href=\"https://codepen.io/matthewdang131/pen/dmbyRL\">https://codepen.io/matthewdang131/pen/dmbyRL</a></p>","frontmatter":{"date":"March 08, 2018","path":"/ownego-exercise","title":"Ownego Exercise"}}},"pathContext":{}}

